.right_head .navigation .menu_list {
  list-style: none;
  /* display: grid; */
  display: INLINE-FLEX;
  JUSTIFY-CONTENT: space-between;
  CURSOR: POINTER;
  padding: inherit;
  /* width: 650px; */
  grid-gap: 60px;
  gap: 60px;
  font-size: 1.5rem;
  grid-template-columns: repeat(2, 1fr) repeat(2, 1.5fr);
  place-items: center;
  margin-right: 30px;
  /* font-family: "Roboto", sans-serif; */
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: bold;
  text-transform: uppercase;
}

.right_head .navigation .menu_list .items {
  text-align: center;
  position: relative;
}

.triangle {
  position: relative;
}

.triangle::before {
  content: ".";
  width: 100px;
  background-color: red;
  position: absolute;
  color: transparent;
  -webkit-clip-path: polygon(0 0, 15% 0, 0 40%);
          clip-path: polygon(0 0, 15% 0, 0 40%);
  left: -16px;
  top: 4px;
  display: none;
}

.items:hover .triangle {
  text-decoration: underline;
  color: black;
}

.items:hover .triangle::before {
  display: block;
}

